.list-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5px;
}

.list-item {
    list-style-type: none;
    border: 1px solid rgb(161, 158, 158);
    padding: 5px;
    padding-left: 5px;
    border-radius: 5px;
    width: 90%;
    height: fit-content;
    display: flex;
    align-items: center;
    position: relative;
    margin: 10px;

}

.icons {
    position: absolute;
    right: 10px;
}

.icons i {
    margin: 4px;
    font-size: large; 
    cursor: pointer;
    transition: 0.1s;
}

.icons i:hover {
    transform: scale(1.2);
}

.icon-delete:hover {
    color: red;
}