.input-container {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    
  }
  
  .input-box-todo {
    margin-left: 10px;
    width: 80%; 
    height: 40px;
    border-radius: 14px;
    border: 1px solid black;
    padding-left: 10px;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.3);
    outline: none;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: large;
    
  }
  
  .add-btn {
    width: 40px;
    height: 40px;
    border-radius: 50%; 
    border: none;
    background-color: #316fc1;
    color: white;
    font-weight: bolder;
    font-size: larger;
    margin-left: 10px;
    cursor: pointer;
    box-shadow: 0px 5px 10px rgba(11, 10, 10, 0.3);
    transition: 0.3s;
  }
  
  .add-btn:active {
    box-shadow: none;
  }