* {
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(90deg, #00C9FF 0%, #92FE9D 100%);
}

.main-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;

}

.center-container {
  min-height: 500px;
  width: 350px;
  width: fit-content;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 20px;
}

.app-heading {
  padding: 5px;
  text-align: center;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: uppercase;
}

